.digidoc_card1 {
  display: flex;
  flex-direction: column;
  background: #f5faff;

  border: 1px solid #d0d7e2;
  border-radius: 10px;

  padding: 10px;
}
.digidoc_card_img img {
  object-fit: cover;
}
.digi_first_card_content {
  display: flex;
  justify-content: space-between;
}

.digidoc_about_desc {
  width: 60%;
  padding-left: 10px;
  padding-top: 10px;
}

.digidoc_about_desc p {
  font-size: 14px;
  line-height: 20px;
  color: #596579;
  text-align: left;
}

.digidoc_card_but {
  display: flex;
  align-items: flex-end;
}

#digidoc_card_but {
  height: 40px;
  width: 130px;
  border: 1px solid #2745f0;
  color: #2745f0;
  background: white;
  border-radius: 5px;
  font-weight: 500;
  font-size: 14px;
  line-height: 15px;
  transition: 0.5s;
}

#digidoc_card_but:hover {
  background-color: #2745f0;
  color: #fff;
}

@media screen and (max-width: 900px) {
  #digidoc_card_but {
    width: fit-content;
  }
}

@media screen and (max-width: 600px) {
  .digidoc_card_but {
    width: 60%;
  }
  .digidoc_about_desc {
    padding-left: 0;
    width: 80%;
  }
  .digidoc_about_desc p {
    text-align: center;
  }
  #digidoc_card_but {
    width: 100%;
    margin-top: 10%;
  }
}
