.main-panel {
  z-index: -1;
}

.overlay {
  box-shadow: 2px 2px 10px black;
  position: absolute;
  top: 0%;
  z-index: 100;
  background-color: white;
  width: 100%;
  transition: 0.5s;
}
.lab-test-report {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.lab-test-report img {
  width: 80%;
  margin: 20px;
}
.overlay:hover {
  box-shadow: 2px 2px 20px rgb(58, 58, 58);
  transform: scale(1.01);
}
.close {
  position: absolute;
  bottom: 96%;
  cursor: pointer;
  right: 3%;
  font-size: 4.5rem;
}
